$yellow: #ebc634;
@import '~bootstrap/dist/css/bootstrap.min.css';

body {
  top: 0;
  bottom: 0;
  margin: 0;
  padding: 0;
  border: 0;
}

.navBox {
  background: linear-gradient(to bottom, black 0%, rgba(0,0,0,0) 80%);
  width: 100vw;
  position: fixed;
  margin: 0px;
  padding-top: 8px;
  overflow: hidden;
  z-index: 99999;
  -webkit-user-select: none; /* Safari */        
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* IE10+/Edge */
  user-select: none; /* Standard */
}

.navBox a {
  display: inline;
  /*padding: 0 2px;*/
  margin: 0 18px;
  color: #ebc634;
  font-size: 20px;
  cursor: pointer;
}

.underline:hover {
  text-decoration: underline;
}

.navBox > .scrollLinks
{
  color: #ebc634;
  width: 72vw;
  float: left;
}

.navBox > .outLinks
{
  float: right;
  width: 25vw;
  color: #ebc634;
  font-size: 20px;
  overflow: hidden;
  text-align: center;
}

.navBox > .outLinks > .resume {
  text-decoration: none;
  text-align: center;
  margin: auto;
  
}

.outLinkPicture {
  vertical-align: middle;
  width: 20px;
  height: 20px;
}

.navBox > .active {
  border-bottom: 20px solid #ebc634;
}

.section {
  height: 101vh;
  width: 100vw;
}

.home {
  background-image: url('./images/treesandstars2.jpg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

h1.name {
  color: #ebc634;
  font-size: 60px;
}

h3.title, .header-box {
  color: #ebc634;
  font-size: 22px;
}

div.heading {
  top: 30%;
  left: 20%;
  position: absolute;
}

.about {
  background-color: #39545f;
  padding-top: 3%;
  padding-bottom: 3%;
}

.projects {
  background-color: #38454b;
  padding-top: 3%;
  padding-bottom: 3%;
} 

.contact {
  display: inline;
  /*padding: 0 2px;*/
  margin: 0 18px;
  color: #ebc634;
  font-size: 20px;
  cursor: pointer;
}

.skills {
  vertical-align: middle;
  padding: 30px;
  border: 3px solid #ebc634;
  border-collapse: collapse;
}

.skills > table {
  margin-left: auto;
  margin-right: auto;
}
.skills > table, th, td {
  color: #ebc634;
  padding: 2px;
  border: 3px solid #ebc634;
  border-collapse: collapse;
}

.skills > th, td {
  white-space: nowrap;
  list-style-position: inside;
  padding: 20px 20px 20px 0px;
}

th.header-box {
  text-align: center;
}

.list-header {
  padding-left: 0;
  font-size: 20px;
  font-style: italic;
  list-style: none;
}

.ssection-header {
  color: #ebc634;
  text-align: left;
  padding-top: 20%;
  font-size: 48px;
}

.portrait {
  margin-top: 8%;
  border-radius: 50%;
  border: 4px solid #ebc634;
  /*width: 16em;*/
  width: auto;
  max-width: 16em;
  display: block;
  margin-left: auto;
  margin-right: auto;
  
}

.card { 
  width: auto;
  max-width: 612px;
  max-height: 350px;
}

.project-description {
  color: #ebc634;
}

.buttons {
  margin: 10px 25px 10px 0px;
}

.dots {
  color: #ebc634;
  text-align: center;
  font-size: 88px;
}

.left-col {
  padding-right: 10%;
}

.right-col {
  padding-left: 10%;
}